import { ICON_PATH } from './../../../content/const/icon-path.const';
import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-date-show',
  templateUrl: './date-show.component.html',
  styleUrls: ['./date-show.component.scss']
})
export class DateShowComponent implements OnInit {
  @Input() startDate = '';
  @Input() endDate = '';
  sameDate = true;

  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }
  constructor() { }

  ngOnInit(): void {
    this.sameDate = this.startDate === this.endDate;
  }

}
