import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { DateType, SwiperDatePickerService, TaiwanDatePipe } from 'merc-lib';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class DatePickService {

  constructor(
    private swiperDatePickerService: SwiperDatePickerService,
    private taiwanDatePipe: TaiwanDatePipe
  ) { }
  /*
   * 表單顯示日期，非區間不需要傳迄日
   * @param form form
   * @param startName 起日formControlName(不加display)
   * @endName 迄日formControlName(不加display)
   * @isStart 迄日需傳入false
   */
  onDateClick(form: FormGroup, startName: string, endName?: string, isStart = true): void {
    const formValue = form.getRawValue();
    if (endName) {
      // 點擊起日
      if (isStart) {
        const defaultDate = formValue[startName] || moment().subtract(2, 'M').valueOf();
        this.swiperDatePickerService.openDatePicker({ defaultDate, dateType: DateType.startDate }).afterClose.subscribe(date => {
          if (date) {
            form.get(`display_${startName}`)?.patchValue(this.taiwanDatePipe.transform(date, true));
            form.get(startName)?.patchValue(date);

            // 如果大於迄日則重置迄日
            const endDate = formValue[endName];
            if (endDate && moment(endDate).isBefore(date)) {
              form.get(`display_${endName}`)?.patchValue('');
              form.get(endName)?.patchValue('');
            }
          }
        });
      } else {
        // 點擊迄日
        const defaultDate = formValue[endName] || formValue[startName];
        const minDate = formValue[startName] || null;

        this.swiperDatePickerService.openDatePicker({ defaultDate, minDate, dateType: DateType.endDate }).afterClose.subscribe(date => {
          if (date) {
            form.get(`display_${endName}`)?.patchValue(this.taiwanDatePipe.transform(date, true));
            form.get(endName)?.patchValue(date);
          }
        });
      }
    } else {
      const defaultDate = formValue[startName] || moment().valueOf();
      this.swiperDatePickerService.openDatePicker({ defaultDate, dateType: DateType.startDate }).afterClose.subscribe(date => {
        if (date) {
          form.get(`display_${startName}`)?.patchValue(this.taiwanDatePipe.transform(date, true));
          form.get(startName)?.patchValue(date);
        }
      });
    }

  }
}
