import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Component, DoCheck, ElementRef, EventEmitter, HostListener, Input, OnInit, Output, Renderer2, ViewChild, AfterViewInit, HostBinding } from '@angular/core';
import { ICON_PATH } from '../../const/icon-path.const';

import { Steps } from '../step';

@Component({
  selector: 'lib-running-step',
  templateUrl: './running-step.component.html',
  styleUrls: ['./running-step.component.scss'],
})
export class RunningStepComponent implements OnInit, DoCheck, AfterViewInit {
  @HostBinding('class') class = 'step-cmpt';
  @ViewChild('stepBox') stepBox: ElementRef | undefined;
  @ViewChild('barBox') barBox: ElementRef | undefined;
  /** 進度條 */
  @ViewChild('progressBar') progressBar: ElementRef | undefined;
  /** 已完成進度條 */
  @ViewChild('doneBar') doneBar: ElementRef | undefined;
  /** 小人位置 */
  @ViewChild('runBox') runBox: ElementRef | undefined;

  get ICON_PATH(): typeof ICON_PATH {
    return ICON_PATH;
  }

  @Output() stepChange = new EventEmitter<number>();

  @Input() stepsName: string[] = [];

  @Input() title = '請輸入標題';

  @Input() isReadOnly = false;
  /** 顯示返回列表 */
  @Input() showBack = true;
  /** 返回列表url */
  @Input() url?: string;
  /** 顯示列表param */
  @Input() param?: any;

  currentStep = 0;
  /** 同一步不需更新畫面 */
  sameStep = true;
  /** 加工過的step資訊 */
  steps: Steps[] = [];
  /** 整個bar */
  barBoxWidth = 0;
  /** 步驟項寬度 */
  stepBoxWidth = 0;
  /** 步驟space-evenly的寬度 */
  gapWidth = 0;
  /** 總共幾步 */
  stepsCounts = 0;
  // TODO:性別根據登入調整
  gender = '1';

  @HostListener('window:resize')

  onResize(): void {
    this.initAllWidth();
  }

  constructor(
    private renderer: Renderer2,
    private location: Location,
    private route: Router
  ) { }

  ngOnInit(): void {
    console.log('initSteps');
    
    this.initSteps();
    this.stepsCounts = this.stepsName.length;
  }

  ngDoCheck(): void {
    if (!this.sameStep) {
      if (this.doneBar?.nativeElement) {
        this.setDoneBar(this.currentStep);
      }
      if (this.runBox?.nativeElement) {
        this.setRunningMan(this.currentStep);
      }
    }
    this.sameStep = true;
  }

  ngAfterViewInit(): void {
    this.initAllWidth();
  }

  /**
   * 加工step
   */

  initSteps(): void {
    this.steps = this.stepsName.map((step, i) => ({
      title: step,
      titleStep: i,
      icon: `./assets/image/btn/step/btn_${i + 1}_default.svg`,
      disable_icon: `./assets/image/btn/step/btn_${i + 1}_disable.svg`,
      show: true
    }));

  }

  /** 先算出所有參數寬度 */
  initAllWidth(): void {
    this.barBoxWidth = this.barBox?.nativeElement.offsetWidth;
    this.stepBoxWidth = this.stepBox?.nativeElement.children[0].offsetWidth;
    this.gapWidth = (this.barBoxWidth - 60 - (this.stepBoxWidth * this.stepsCounts)) / (this.stepsCounts + 1);
    this.initProgressBarWidth();
    this.setDoneBar(this.currentStep);
    this.setRunningMan(this.currentStep);
  }

  /**
   *  progress bar 長度
   */
  initProgressBarWidth(): void {
    const barWidth = (this.stepBoxWidth + this.gapWidth) * (this.stepsCounts - 1);
    this.renderer.setStyle(this.progressBar?.nativeElement, 'width', `${barWidth}px`);
  }

  /**
   * 完成進度條長度
   */

  setDoneBar(currentStep: number): void {
    let length = 0;
    if (currentStep + 1 < this.stepsCounts) {
      length = (this.stepBoxWidth + this.gapWidth) * (currentStep + 0.5);
    } else {
      length = (this.stepBoxWidth + this.gapWidth) * (this.stepsCounts - 1);
    }
    this.renderer.setStyle(this.doneBar?.nativeElement, 'width', `${length}px`);
  }

  /** 跑跑人位移 */
  setRunningMan(currentStep: number): void {
    const runBox = this.runBox?.nativeElement.children[0];
    const fromStart = this.stepBoxWidth * 0.5 + this.gapWidth;
    let length = 0;
    if (currentStep + 1 < this.stepsCounts) {
      length = (this.stepBoxWidth + this.gapWidth) * (currentStep + 0.5) + fromStart;
    } else {
      length = (this.stepBoxWidth + this.gapWidth) * (this.stepsCounts - 1) + fromStart + 40;
    }
    // 跑跑人移動時在數字上層
    this.renderer.setStyle(this.runBox?.nativeElement, 'z-index', '401');
    this.renderer.setStyle(runBox, 'marginLeft', `${length}px`);
    setTimeout(() => {
      // 跑跑人移動完放下層，才能選到icon
      this.renderer.setStyle(this.runBox?.nativeElement, 'z-index', '200');
    }, 600);
  }

  /** 下一步 */
  next(step: number): void {
    this.sameStep = step === this.currentStep;
    this.currentStep = step;
  }

  move(step: number): void {
    if (step < 0 || step > this.stepsCounts - 1) { return; }
    this.sameStep = step === this.currentStep;
    this.currentStep = step;
  }

  /** 前一步 */
  prev(step: number): void {
    if (step < 0) { return; }
    this.sameStep = step === this.currentStep;
    this.currentStep = step;
  }

  /** 跳至step步驟 */
  goToStep(step: number): void {
    this.stepChange.emit(step);
  }

  /** 回上頁 */
  goBack(): void {
    if (!this.showBack) { return; }
    if (this.url) {
      this.route.navigate([this.url], { queryParams: this.param });
    } else {
      this.location.back();
    }
  }
}
