import { Pipe, PipeTransform } from '@angular/core';
import { SignUpStatus } from '../../core/enums/signup-status.enum';

@Pipe({
  name: 'courseStatus'
})
export class CourseStatusPipe implements PipeTransform {

  transform(value: number, ...args: unknown[]): string {
    switch (value) {
      case SignUpStatus.unOpen:
        return '未開放';
      case SignUpStatus.isOpen:
        return '我要報名';
      case SignUpStatus.full:
        return '已額滿';
      case SignUpStatus.closed:
        return '已截止';
      case SignUpStatus.signed:
        return '取消報名';
      case SignUpStatus.rejected:
        return '報名失敗';
      default:
        return '未定義';
    }
  }

}
